@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;600&display=swap');

*{
    margin: 0;
    padding: 0;
}

body{
    background: #fff;
    color: #222;
    font-family: 'Montserrat', sans-serif;
    font-weight: 300;
}

.wrapper{
    width: 1280px;
    margin: 50px auto;
}

header .logo{
    font-weight: 600;
    font-size: 20px;
}

header ul.nav{
    float: right;

}

header ul.nav li{
    display: inline;
    margin-left: 25px;
    cursor: pointer;
    transition: opacity 400ms ease;
}

header ul.nav li:hover{
    opacity: 0.6;
}

header .presentation{
    margin: 50px 0;
    background: url('./img/back.jpg') no-repeat;
    width: 100%;
    height: 900px;
    background-size: cover;
    background-position: center center;
    background-blend-mode: multiply;
    background-color: pink;
    position: relative;
}

header .presentation:after{
    content: 'Очки с недочетами по скидке';
    position: absolute;
    top: 100px;
    left: 50px;
    font-size: 40px;
    font-weight: 600;
    width: 300px;
    color: #fff;
}

header .presentation:before{
    content: 'от -40% до -80%';
    position: absolute;
    top: 240px;
    left: 50px;
    font-size: 15px;
    font-weight: 600;
    width: 300px;
    color: #fff;
}

footer{
    text-align: center;
    margin-top: 50px;
}